import { useUser } from '../../context/UserContext';
import React from 'react';
import confetti from 'canvas-confetti';
import { GameProvider, useGame } from '../../context/GameContext';
import GameShell from '../GameUI/GameShell';
import Button from '../Button/Button';
import * as styles from '../TypeTheWord/TypeTheWord.module.css'; // Reusing styles

interface OriginWord {
  word: string;
  origin: string; // The correct answer
  options: string[]; // 4 choices
}

interface PickTheOriginProps {
  words: OriginWord[];
}

const PickTheOriginContent: React.FC<PickTheOriginProps> = ({ words }) => {
  const { state, dispatch } = useGame();
  const { user, updateProgress, incrementStreak } = useUser();
  const currentWord = words[state.currentRound];

  const handleChoice = (choice: string) => {
    if (state.status !== `playing`) return;

    const isCorrect = choice === currentWord.origin;

    dispatch({
      type: `SUBMIT_ANSWER`,
      isCorrect,
      question: currentWord.word,
      userAnswer: choice,
      correctAnswer: currentWord.origin,
    });

    if (isCorrect) {
      confetti({
        particleCount: 50,
        spread: 60,
        origin: { y: 0.7 },
      });

      // Update stats
      updateProgress({ wordsMastered: user.wordsMastered + 1 });

      // Check if this is the last round
      if (state.currentRound === words.length - 1) {
        updateProgress({ labsCompleted: user.labsCompleted + 1 });
        incrementStreak();
      }
    }

    // Auto advance after short delay
    setTimeout(() => {
      dispatch({ type: `NEXT_ROUND` });
    }, 1500);
  };

  if (!currentWord) return null;

  return (
    <GameShell
      title="Pick The Origin"
      description="Can you guess the language of origin?"
      onStart={() =>
        dispatch({ type: `START_GAME`, totalRounds: words.length })
      }
    >
      <div className={styles.playArea}>
        <h3 style={{ fontSize: `2rem`, margin: `1rem 0` }}>
          {currentWord.word}
        </h3>

        <div
          style={{
            display: `grid`,
            gridTemplateColumns: `1fr 1fr`,
            gap: `1rem`,
            width: `100%`,
          }}
        >
          {currentWord.options.map((option) => {
            let btnVariant: 'secondary' | 'primary' | 'danger' = `secondary`;
            // If we answered, show result
            if (state.status === `success` || state.status === `error`) {
              if (option === currentWord.origin)
                btnVariant = `primary`; // Show correct
              else if (
                option !== currentWord.origin &&
                state.status === `error`
              )
                btnVariant = `secondary`; // Keep others neutral
              // If this was the wrong choice user picked
              // We don't track *which* wrong choice in history easily here without local state,
              // but for simplicity we rely on the primary indicating the right answer.
            }

            return (
              <Button
                key={option}
                variant={btnVariant}
                onClick={() => handleChoice(option)}
                disabled={state.status !== `playing`}
              >
                {option}
              </Button>
            );
          })}
        </div>

        <div
          className={`${styles.feedback} ${
            state.status !== `playing` ? styles[state.status] : ``
          }`}
        >
          {state.status === `success` && `Correct!`}
          {state.status === `error` &&
            `Incorrect. It was ${currentWord.origin}.`}
        </div>
      </div>
    </GameShell>
  );
};

const PickTheOrigin: React.FC<PickTheOriginProps> = (props) => (
  <GameProvider>
    <PickTheOriginContent {...props} />
  </GameProvider>
);

export default PickTheOrigin;
