import React from 'react';
import Button from '../Button/Button';
import { useGame } from '../../context/GameContext';
import * as styles from '../TypeTheWord/TypeTheWord.module.css'; // Reusing styles for now

interface GameShellProps {
  title: string;
  description: React.ReactNode;
  onStart: () => void;
  // onNext removed as it is handled by children/game loop
  children: React.ReactNode;
}

const GameShell: React.FC<GameShellProps> = ({
  title,
  description,
  onStart,
  children,
}) => {
  const { state, dispatch } = useGame();

  if (state.status === `idle`) {
    return (
      <div className={styles.container} style={{ textAlign: `center` }}>
        <h2>{title}</h2>
        <div
          style={{
            marginBottom: `1.5rem`,
            color: `var(--color-text-secondary)`,
          }}
        >
          {description}
        </div>
        <Button variant="primary" onClick={onStart}>
          Start Game
        </Button>
      </div>
    );
  }

  if (state.status === `finished`) {
    const percentage = Math.round((state.score / state.totalRounds) * 100);
    return (
      <div className={styles.container}>
        <h2>Game Complete! üéâ</h2>
        <div
          style={{
            fontSize: `3rem`,
            fontWeight: `bold`,
            margin: `1rem 0`,
            color:
              percentage > 70
                ? `var(--color-success)`
                : `var(--color-text-primary)`,
          }}
        >
          {percentage}%
        </div>
        <p>
          You got {state.score} out of {state.totalRounds} correct.
        </p>

        {state.history.length > 0 && (
          <table className={styles.summaryTable}>
            <thead>
              <tr>
                <th>Word</th>
                <th>Result</th>
              </tr>
            </thead>
            <tbody>
              {state.history.map((item, idx) => (
                <tr key={idx}>
                  <td>{item.question}</td>
                  <td>
                    {item.isCorrect ? `‚úÖ` : `‚ùå (Ans: ${item.correctAnswer})`}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}

        <div style={{ marginTop: `2rem` }}>
          <Button variant="primary" onClick={() => dispatch({ type: `RESET` })}>
            Play Again
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div
        className={styles.progress}
        style={{ display: `flex`, justifyContent: `space-between` }}
      >
        <span>
          Round {state.currentRound + 1} / {state.totalRounds}
        </span>
        <span>Score: {state.score}</span>
      </div>

      {children}
    </div>
  );
};

export default GameShell;
