import { useUser } from '../../context/UserContext';
import React, { useState, useEffect } from 'react';
import confetti from 'canvas-confetti';
import { GameProvider, useGame } from '../../context/GameContext';
import GameShell from '../GameUI/GameShell';
import * as styles from './RootMatch.module.css';

interface RootPair {
  root: string;
  meaning: string;
}

interface RootMatchProps {
  rounds: { pairs: RootPair[] }[]; // Each round has a set of pairs
}

const RootMatchContent: React.FC<RootMatchProps> = ({ rounds }) => {
  const { state, dispatch } = useGame();
  const { user, updateProgress, incrementStreak } = useUser();

  // Local state for the current interaction
  const [selectedRoot, setSelectedRoot] = useState<string | null>(null);
  const [selectedMeaning, setSelectedMeaning] = useState<string | null>(null);
  const [matchedPairs, setMatchedPairs] = useState<string[]>([]); // Array of matched roots
  const [mismatched, setMismatched] = useState(false);

  const currentRoundData = rounds[state.currentRound];

  // Reset local state on new round
  useEffect(() => {
    setSelectedRoot(null);
    setSelectedMeaning(null);
    setMatchedPairs([]);
    setMismatched(false);
  }, [state.currentRound]);

  // Check for completion
  useEffect(() => {
    if (state.status === `playing` && currentRoundData) {
      if (matchedPairs.length === currentRoundData.pairs.length) {
        // Round Complete
        confetti({
          particleCount: 50,
          spread: 60,
          origin: { y: 0.6 },
        });

        // Update stats - Count a completed matching board as 5 "words" mastered roughly, or just 1?
        // Let's count it as the number of pairs.
        const pairsCount = currentRoundData.pairs.length;
        updateProgress({ wordsMastered: user.wordsMastered + pairsCount });

        // If last round
        if (state.currentRound === rounds.length - 1) {
          updateProgress({ labsCompleted: user.labsCompleted + 1 });
          incrementStreak();
        }

        dispatch({
          type: `SUBMIT_ANSWER`,
          isCorrect: true,
          question: `Round ${state.currentRound + 1}`,
          userAnswer: `Matched All`,
          correctAnswer: `Matched All`,
        });

        setTimeout(() => {
          dispatch({ type: `NEXT_ROUND` });
        }, 1500);
      }
    }
  }, [
    matchedPairs,
    currentRoundData,
    state.status,
    dispatch,
    state.currentRound,
    user.wordsMastered,
    user.labsCompleted,
    updateProgress,
    incrementStreak,
    rounds.length,
  ]);

  const handleRootClick = (root: string) => {
    if (matchedPairs.includes(root)) return;
    setMismatched(false);
    setSelectedRoot(root);
    checkMatch(root, selectedMeaning);
  };

  const handleMeaningClick = (meaning: string) => {
    // Find which root this meaning belongs to, to check if it's already matched
    const pair = currentRoundData.pairs.find((p) => p.meaning === meaning);
    if (!pair || matchedPairs.includes(pair.root)) return;

    setMismatched(false);
    setSelectedMeaning(meaning);
    checkMatch(selectedRoot, meaning);
  };

  const checkMatch = (root: string | null, meaning: string | null) => {
    if (!root || !meaning) return;

    const correctPair = currentRoundData.pairs.find(
      (p) => p.root === root && p.meaning === meaning,
    );

    if (correctPair) {
      // Match!
      setMatchedPairs((prev) => [...prev, root]);
      setSelectedRoot(null);
      setSelectedMeaning(null);
    } else {
      // Mismatch
      setMismatched(true);
      setTimeout(() => {
        setSelectedRoot(null);
        setSelectedMeaning(null);
        setMismatched(false);
      }, 500);
    }
  };

  // Memoizing shuffled lists
  /* eslint-disable react-hooks/exhaustive-deps */
  const shuffledMeanings = React.useMemo(() => {
    if (!currentRoundData) return [];
    return [...currentRoundData.pairs].sort(() => Math.random() - 0.5);
  }, [currentRoundData]);
  /* eslint-enable react-hooks/exhaustive-deps */

  if (!currentRoundData) return null;

  return (
    <GameShell
      title="Root Match"
      description="Tap a Root, then tap its Meaning."
      onStart={() =>
        dispatch({ type: `START_GAME`, totalRounds: rounds.length })
      }
    >
      <div className={styles.container}>
        <div className={styles.grid}>
          {/* Roots Column */}
          <div className={styles.column}>
            <h4 style={{ textAlign: `center`, marginBottom: `0.5rem` }}>
              Roots
            </h4>
            {currentRoundData.pairs.map((pair) => (
              <div
                key={pair.root}
                className={`
                  ${styles.item}
                  ${selectedRoot === pair.root ? styles.selected : ``}
                  ${matchedPairs.includes(pair.root) ? styles.matched : ``}
                  ${
                    selectedRoot === pair.root && mismatched ? styles.shake : ``
                  }
                `}
                onClick={() => handleRootClick(pair.root)}
              >
                {pair.root}
              </div>
            ))}
          </div>

          {/* Meanings Column */}
          <div className={styles.column}>
            <h4 style={{ textAlign: `center`, marginBottom: `0.5rem` }}>
              Meanings
            </h4>
            {shuffledMeanings.map((pair) => (
              <div
                key={pair.meaning}
                className={`
                  ${styles.item}
                  ${selectedMeaning === pair.meaning ? styles.selected : ``}
                  ${matchedPairs.includes(pair.root) ? styles.matched : ``}
                  ${
                    selectedMeaning === pair.meaning && mismatched
                      ? styles.shake
                      : ``
                  }
                `}
                onClick={() => handleMeaningClick(pair.meaning)}
              >
                {pair.meaning}
              </div>
            ))}
          </div>
        </div>
      </div>
    </GameShell>
  );
};

const RootMatch: React.FC<RootMatchProps> = (props) => (
  <GameProvider>
    <RootMatchContent {...props} />
  </GameProvider>
);

export default RootMatch;
