import { useUser } from '../../context/UserContext';
import React, { useState } from 'react';
import confetti from 'canvas-confetti';
import { GameProvider, useGame } from '../../context/GameContext';
import GameShell from '../GameUI/GameShell';
import * as styles from './OddOneOut.module.css';

interface OddOption {
  text: string;
  isOdd: boolean;
  reason: string; // The explanation for why it is odd (or why others are similar)
}

interface OddOneOutProps {
  rounds: { options: OddOption[] }[];
}

const OddOneOutContent: React.FC<OddOneOutProps> = ({ rounds }) => {
  const { state, dispatch } = useGame();
  const { user, updateProgress, incrementStreak } = useUser();
  const [selectedOption, setSelectedOption] = useState<number | null>(null);

  const currentRoundData = rounds[state.currentRound];

  const handleSelect = (index: number) => {
    if (state.status !== `playing`) return;

    setSelectedOption(index);
    const option = currentRoundData.options[index];

    const isCorrect = option.isOdd;

    if (isCorrect) {
      confetti({
        particleCount: 50,
        spread: 60,
        origin: { y: 0.6 },
      });

      // Update stats
      updateProgress({ wordsMastered: user.wordsMastered + 1 });

      // Check if this is the last round
      if (state.currentRound === rounds.length - 1) {
        updateProgress({ labsCompleted: user.labsCompleted + 1 });
        incrementStreak();
      }
    }

    dispatch({
      type: `SUBMIT_ANSWER`,
      isCorrect,
      question: `Round ${state.currentRound + 1}`,
      userAnswer: option.text,
      correctAnswer:
        currentRoundData.options.find((o) => o.isOdd)?.text || `Unknown`,
    });

    // Auto next
    setTimeout(() => {
      setSelectedOption(null);
      dispatch({ type: `NEXT_ROUND` });
    }, 2500);
  };

  if (!currentRoundData) return null;

  return (
    <GameShell
      title="Odd One Out"
      description="Find the word that doesn't fit the pattern."
      onStart={() =>
        dispatch({ type: `START_GAME`, totalRounds: rounds.length })
      }
    >
      <div className={styles.container}>
        <div className={styles.grid}>
          {currentRoundData.options.map((option, idx) => {
            // Determine styling based on state
            let statusClass = ``;
            if (state.status === `success` || state.status === `error`) {
              if (option.isOdd) statusClass = styles.correct;
              else if (idx === selectedOption) statusClass = styles.incorrect;
            }

            return (
              <button
                key={idx}
                className={`${styles.option} ${statusClass}`}
                onClick={() => handleSelect(idx)}
                disabled={state.status !== `playing`}
              >
                {option.text}
              </button>
            );
          })}
        </div>

        {(state.status === `success` || state.status === `error`) && (
          <div className={styles.reason}>
            <strong>
              {currentRoundData.options.find((o) => o.isOdd)?.reason}
            </strong>
          </div>
        )}
      </div>
    </GameShell>
  );
};

const OddOneOut: React.FC<OddOneOutProps> = (props) => (
  <GameProvider>
    <OddOneOutContent {...props} />
  </GameProvider>
);

export default OddOneOut;
