import React, { createContext, useReducer, useContext } from 'react';

// --- Types ---

export type GameState = 'idle' | 'playing' | 'success' | 'error' | 'finished';

export interface GameHistoryItem {
  question: string;
  userAnswer: string;
  isCorrect: boolean;
  correctAnswer: string;
}

export interface GameStateData {
  status: GameState;
  currentRound: number; // 0-indexed
  score: number;
  totalRounds: number;
  feedbackMessage?: string;
  history: GameHistoryItem[];
}

type Action =
  | { type: 'START_GAME'; totalRounds: number }
  | {
      type: 'SUBMIT_ANSWER';
      isCorrect: boolean;
      question: string;
      userAnswer: string;
      correctAnswer: string;
    }
  | { type: 'NEXT_ROUND' }
  | { type: 'RESET' };

// --- Reducer ---

const initialState: GameStateData = {
  status: `idle`,
  currentRound: 0,
  score: 0,
  totalRounds: 10,
  history: [],
};

const gameReducer = (state: GameStateData, action: Action): GameStateData => {
  switch (action.type) {
    case `START_GAME`:
      return {
        ...initialState,
        status: `playing`,
        totalRounds: action.totalRounds,
      };

    case `SUBMIT_ANSWER`:
      return {
        ...state,
        status: action.isCorrect ? `success` : `error`,
        score: action.isCorrect ? state.score + 1 : state.score,
        history: [
          ...state.history,
          {
            question: action.question,
            userAnswer: action.userAnswer,
            isCorrect: action.isCorrect,
            correctAnswer: action.correctAnswer,
          },
        ],
      };

    case `NEXT_ROUND`:
      if (state.currentRound >= state.totalRounds - 1) {
        return { ...state, status: `finished` };
      }
      return {
        ...state,
        status: `playing`,
        currentRound: state.currentRound + 1,
      };

    case `RESET`:
      return initialState;

    default:
      return state;
  }
};

// --- Context ---

interface GameContextType {
  state: GameStateData;
  dispatch: React.Dispatch<Action>;
}

const GameContext = createContext<GameContextType | undefined>(undefined);

export const GameProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [state, dispatch] = useReducer(gameReducer, initialState);
  return (
    <GameContext.Provider value={{ state, dispatch }}>
      {children}
    </GameContext.Provider>
  );
};

export const useGame = () => {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error(`useGame must be used within a GameProvider`);
  }
  return context;
};
